<template>
  <div id="FocusCenter">
    <header-nav :title="msg"></header-nav>
    <ul class="attentionList" v-if="list.length !== 0">
      <li class="clear border-1px" v-for="(item, index) in list" :key="item.mid" @click="toPage(item)">
        <div class="fl">
          <img :src="item.logo" alt=""
           onerror="javascript:this.src='https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/default/avatar.png';">
        </div>
        <div class="fl">{{item.name}}</div>
        <div class="fr" v-show="item.bol" @click.stop="isFollow(index)">√ 已关注</div>
        <div class="fr" v-show="!item.bol" @click.stop="isFollow(index)">+ 关注</div>
      </li>
    </ul>
    <div id="noList" v-if="list.length === 0">
      <stateless-head :headData="headData"></stateless-head>
      <news-list :list="recommendList"></news-list>
    </div>
  </div>
</template>

<script>
import HeaderNav from '../common/header-nav'
import NewsList from '../common/news-list'
import StatelessHead from '../common/stateless-head'

import { mapState } from 'vuex'

export default {
  name: 'FocusCenter',
  computed: mapState({
    commonState: state => state.common
  }),
  data () {
    return {
      msg: '我的关注',
      list: [],          // 关注列表
      recommendList: [], // 推荐列表
      headData: {
        imgUrl: require('../../assets/img/icon-focus.png'),
        txt: '暂无关注'
      }
    }
  },
  methods: {
    // 是否关注
    isFollow (index) {
      let data = {}
      data.mid = this.list[index].mid
      data.status = this.list[index].bol ? 2 : 1
      this.$api.post('IS_FOLLOW', data, (res) => {
        this.getList()
        // this.list[index].bol = !this.list[index].bol
        this.$utils.refreshAttention(data.status, data.mid)
      })
    },
    // 条状媒体号
    toPage (item) {
      this.$router.push({path: '/MediaNumber/' + item.mid})
    },
    // 获取关注列表
    getList () {
      this.$api.get('FOLLOW_LIST', {page: 1}, (res) => {
        for (let i in res.data.data) {
          res.data.data[i].bol = true
        }
        this.list = res.data.data || []
        if (this.list.length === 0) {
          this.getRecommend()
        }
      })
    },
    // 上拉函数
    upList () {
      if (this.$utils.scrollHeight() === 0) {
        let data = {}
        data.columnid = 'index'
        data.lastid = this.recommendList[(this.recommendList.length - 1)].id
        this.$api.get('PULL_UP', data, (res) => {
          let list = res.data || []
          this.recommendList = [...this.recommendList, ...list]
        })
      }
    },
    // 获取推荐阅读
    getRecommend () {
      let data = {}
      data.columnid = 'index'
      this.$api.get('PULL', data, (res) => {
        this.recommendList = res.data || []
        window.onscroll = this.upList
      })
    },
    initData () {
      this.getList()
    }
  },
  created () {
    this.initData()
  },
  watch: {
    // 监听如果登录弹框被关闭
    'commonState.login_box' () {
      if (!this.commonState.login_box && this.commonState.login_bol) {
        this.initData()
      }
    }
  },
  components: {
    HeaderNav,
    NewsList,
    StatelessHead
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss" type="text/css">
#FocusCenter{
  .attentionList{
    li{
      line-height: 150px;
      color: #232323;
      font-size: 30px;
      div:nth-of-type(1){
        width: 92px;
        height: 92px;
        border-radius: 50%;
        margin-left: 26px;
        margin-top: 29px;
        overflow: hidden;
        img{
          width: 100%;
          height: 100%;
        }
      }
      div:nth-of-type(2){
        margin-left: 25px;
      }
      div:nth-of-type(3),div:nth-of-type(4){
        width: 130px;
        height: 46px;
        margin-top: 52px;
        margin-right: 30px;
        line-height: 46px;
        color: #fff;
        border-radius: 8px;
        text-align: center;
        background: #bdbcbc;
      }
      div:nth-of-type(4){
        background: #e54256;
      }
    }
  }
}
</style>



