<template>
  <div id="MediaNumber">
    <div class="header clear" v-if="$config.source_style === 6 || $config.source_style === 7">
      <div class="black fl" @click="back">
        <img src="../../assets/img/white_back.png" alt="">
      </div>
      <div class="fl headerName">{{info.name}}的主页</div>
    </div>
    <div class="headers" v-if="$config.source_style === 6 || $config.source_style === 7"></div>
    <div class="top border-1px pB30">
      <div class="logo">
        <img :src="info.logo" alt="">
      </div>
      <div class="name">{{info.name}}</div>
      <div class="btn" @click="isFollow(2)" v-if="info.is_follow === 1">
        √ 已关注
      </div>
      <div class="btns" @click="isFollow(1)" v-if="info.is_follow === 0">
        + 关 注
      </div>
      <div class="title">
        <div class="f28 lh30 jianjie " :class="!info.describe?'text-centerv':''">简介：{{info.describe?info.describe:'该媒体号暂无简介'}}</div>
        <!--<div class="f28 lh30 jianjie">简介：{{info.describe?info.describe:'该媒体号暂无简介'}}</div>-->
      </div>
    </div>
    <div class="list" v-show="list.length !== 0">
      <news-list :list="list"></news-list>
    </div>
    <div class="txt" v-show="list.length === 0">
      暂无动态
      <div class="dynamic">
        <img src="../../assets/img/no-dynamic.png" alt="">
      </div>
    </div>
    <div class="footer clear top-1px" v-if="$route.query.inviteCode" @click="footerBtn">
      <div class="fl">上掘金宝，看多数人不知道的赚钱门路 ></div>
      <div class="fr seeBtn">去看看</div>
    </div>
    <share v-if="shareParams" :shareParams='shareParams'></share>
  </div>
</template>

<script>
import NewsList from '../common/news-list'
import share from '@/components/common/share'
export default {
  name: 'MediaNumber',
  data () {
    return {
      info: {name: '媒体号', logo: '', is_follow: 1, describe: ''},  // 媒体号详情
      list: [], // 列表
      page: 1, // 多少页
      total: 0, // 共多少条
      shareParams: null
    }
  },
  methods: {
    // 点击底层
    footerBtn () {
      if (this.$utils.isWx()) {
        window.location.href = 'http://mp.weixin.qq.com/mp/getmasssendmsg?__biz=MzU2NjY5NTU1NQ==#wechat_webview_type=1&wechat_redirect'
      } else {
        this.$router.push({path: '/'})
      }
    },
    // 是否关注
    isFollow (type) {
      let data = {}
      data.mid = this.$route.params.mid * 1
      data.status = type
      this.$api.post('IS_FOLLOW', data, (res) => {
        this.info.is_follow = this.info.is_follow === 0 ? 1 : 0
        this.$utils.refreshAttention(data.status, data.mid)
        if (this.info.is_follow === 0) {
          this.$toast({
            message: '已取消关注',
            position: 'middle',
            duration: 1500
          })
        } else {
          this.$toast({
            message: '已关注',
            position: 'middle',
            duration: 1500
          })
        }
      })
    },
    // 返回
    back () {
      this.$router.go(-1)
    },
    // 获取媒体号详情
    getInfo () {
      let data = {}
      data.mid = this.$route.params.mid
      this.$api.get('INFO', data, (res) => {
        this.info = res.data
        this.shareParams = {
          title: res.data.name + '的主页',
          desc: `推荐你查看《${res.data.name}》的最新文章，${res.data.describe ? res.data.describe : '暂无简介'}。`,
          link: location.href.indexOf('#') ? location.href.split('#')[0] : location.href,
          imgUrl: res.data.logo ? res.data.logo : '../../assets/img/my_log',
          mid: data.mid
          // imgUrl: '../../assets/img/my_log'
        }
        document.title = this.info.name + '的主页'
      })
    },
    // 获取媒体号列表
    getList () {
      let data = {}
      data.mid = this.$route.params.mid
      data.page = this.page
      this.$api.get('ARTICLE_LIST', data, (res) => {
        this.list = res.data.data || []
        this.total = res.data.total
      })
    },
    // 上拉
    upList () {
      if ((this.total / 10) > this.page) {       // 有剩余分页的时候才可以触发
        if (this.$utils.scrollHeight() === 0) {
          this.page++
        }
      }
    },
    // 初始化
    infoFn () {
      this.getInfo()
      this.getList()
    }
  },
  created () {
    this.infoFn()
    this.$nextTick(() => {
      window.onscroll = this.upList
    })
  },
  watch: {
    // 上拉加载
    page () {
      let data = {}
      data.mid = this.$route.params.mid
      data.page = this.page
      this.$api.get('ARTICLE_LIST', data, (res) => {
        let lists = res.data.data || []
        this.list = [...this.list, ...lists]
      })
    }
  },
  components: {
    NewsList,
    share
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss" type="text/css">
#MediaNumber{
  .footer{
    position: fixed;
    bottom: 0;
    background: #fff;
    line-height: 140px;
    padding: 0 20px;
    font-size: 30px;
    width: 100%;
    box-sizing: border-box;
    .seeBtn{
      border: 1px #bdbcbc solid;
      margin-top: 41px;
      border-radius: 10px;
      line-height: 58px;
      padding: 0 10px;
    }
  }
  .txt{
    margin-top: 30px;
    text-align: center;
    color: #999;
    font-size: 36px;
  }
  .jianjie{
    overflow : hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    text-indent: 32px;
    background: url('../../assets/img/icon-media.png') no-repeat;
    background-size: 22px 26px;
  }
  .list{
    padding-bottom: 140px;
  }
  img{
    width: 100%;
    height: 100%;
  }
  .dynamic{
    width: 268px;
    height: 208px;
    margin: 200px auto;
  }
  .title{
    max-width: 510px;
    margin: 20px auto 0;
    line-height: 34px;
    .imgBox{
      width: 22px;
      height: 26px;
    }
  }
  .headers{
    height: 88px;
  }
  .header{
    background: #FFAA00;
    height: 88px;
    box-sizing: border-box;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 1;
    .headerName{
      line-height: 88px;
      color: #fff;
    }
    .black{
      padding: 29px 30px;
      width: 18px;
      height: 30px;
    }
  }
  .top{
    .logo{
      width: 116px;
      height: 116px;
      margin: 30px auto;
      overflow: hidden;
      border-radius: 50%;
    }
    .name{
      text-align: center;
      margin-bottom: 20px;
    }
    .btn,.btns{
      width: 130px;
      height: 46px;
      font-size: 26px;
      margin-top: 52px;
      margin-right: 30px;
      line-height: 46px;
      color: #fff;
      border-radius: 8px;
      text-align: center;
      background: #bdbcbc;
      margin: 0 auto;
    }
    .btns{
      background: #e54256;
    }
  }
}
.text-centerv{
  width: 340px;
  margin: auto;
}
</style>



